import React, { Component, Suspense } from 'react';
import moment from 'moment';
import { Link } from 'react-router-dom';
class FilterStrip extends Component {

    render() {
        const { assets, feedsFilter, ugcStatusFilter, postTypeFilter, textResetFilter } = this.props;

        const { collection, dateRanges, other, postTags, postType, socialFeed, text, ugcStatus } = assets.filterBody
        console.log("socialFeed", socialFeed)
        return <>
            {((collection && Object.keys(collection).length > 0) || (dateRanges && Object.keys(dateRanges).length > 0) || (postType && Object.keys(postType).length > 0) || (socialFeed && Object.keys(socialFeed).length > 0) || (ugcStatus && Object.keys(ugcStatus).length > 0) || (text && text != "")) ? <div className="container-fluid">
                <div className="tags-group d-flex align-items-center flex-wrap">
                    <div className="block-center badge bg-secondary text-gray-800 rounded-pill border me-2 mb-2">
                        Filter By :
                        <button className="btn p-0 ms-1">
                            <span className="svg-icon me-0">

                            </span>
                        </button>
                    </div>
                    {
                        collection && Object.keys(collection).length > 0 ? Object.keys(collection).map((collectionName) => <div className="block-center badge bg-secondary text-gray-800 rounded-pill border me-2 mb-2">
                            {collectionName}
                            <button className="btn p-0 ms-1">
                                <span className="svg-icon me-0">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M12 10.586l4.95-4.95 1.414 1.414-4.95 4.95 4.95 4.95-1.414 1.414-4.95-4.95-4.95 4.95-1.414-1.414 4.95-4.95-4.95-4.95L7.05 5.636z" fill="currentColor" />
                                    </svg>
                                </span>
                            </button>
                        </div>) : null
                    }
                    {
                        dateRanges && Object.keys(dateRanges).length > 0 ? <div className="block-center badge bg-secondary text-gray-800 rounded-pill border me-2 mb-2">
                            {moment(dateRanges["startDate"]).format("DD-MMM-YYYY")} to {moment(dateRanges["endDate"]).format("DD-MMM-YYYY")}
                            <button className="btn p-0 ms-1" onClick={event => this.props
                                .resetDateFilter()}>
                                <span className="svg-icon me-0">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M12 10.586l4.95-4.95 1.414 1.414-4.95 4.95 4.95 4.95-1.414 1.414-4.95-4.95-4.95 4.95-1.414-1.414 4.95-4.95-4.95-4.95L7.05 5.636z" fill="currentColor" />
                                    </svg>
                                </span>
                            </button>
                        </div> : null
                    }
                    {
                        postType && Object.keys(postType).length > 0 ? Object.keys(postType).map((pType) => <div className="block-center badge bg-secondary text-gray-800 rounded-pill border me-2 mb-2">
                            {pType == "2,4" ? `Image Only` : pType == "3,5" ? `Video Only` : `Post Type`}
                            <button className="btn p-0 ms-1" onClick={event => postTypeFilter(pType)}>
                                <span className="svg-icon me-0">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M12 10.586l4.95-4.95 1.414 1.414-4.95 4.95 4.95 4.95-1.414 1.414-4.95-4.95-4.95 4.95-1.414-1.414 4.95-4.95-4.95-4.95L7.05 5.636z" fill="currentColor" />
                                    </svg>
                                </span>
                            </button>
                        </div>) : null
                    }
                    {
                        socialFeed && Object.keys(socialFeed).length > 0 ? Object.keys(socialFeed).map((socialF) => <div className="block-center badge bg-secondary text-gray-800 rounded-pill border me-2 mb-2" key={socialF}>
                            {socialFeed[socialF]}
                            <button className="btn p-0 ms-1" onClick={event => feedsFilter(socialF, socialFeed[socialF])}>
                                <span className="svg-icon me-0">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M12 10.586l4.95-4.95 1.414 1.414-4.95 4.95 4.95 4.95-1.414 1.414-4.95-4.95-4.95 4.95-1.414-1.414 4.95-4.95-4.95-4.95L7.05 5.636z" fill="currentColor" />
                                    </svg>
                                </span>
                            </button>
                        </div>) : null
                    }
                    {
                        ugcStatus && Object.keys(ugcStatus).length > 0 ? Object.keys(ugcStatus).map((ugcS) => <div className="block-center badge bg-secondary text-gray-800 rounded-pill border me-2 mb-2">
                            {ugcS == 1 ? `Pending Post` : ugcS == 2 ? `Approved Post` : ugcS == 3 ? `Rejected Post` : ugcS == 4 ? `Expired Post` : null}
                            <button className="btn p-0 ms-1" onClick={event => ugcStatusFilter(ugcS)}>
                                <span className="svg-icon me-0">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M12 10.586l4.95-4.95 1.414 1.414-4.95 4.95 4.95 4.95-1.414 1.414-4.95-4.95-4.95 4.95-1.414-1.414 4.95-4.95-4.95-4.95L7.05 5.636z" fill="currentColor" />
                                    </svg>
                                </span>
                            </button>
                        </div>) : null
                    }
                    {
                        text && text != "" ? <div className="block-center badge bg-secondary text-gray-800 rounded-pill border me-2 mb-2">
                            {text}
                            <button className="btn p-0 ms-1" onClick={this.props.textResetFilter}>
                                <span className="svg-icon me-0">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M12 10.586l4.95-4.95 1.414 1.414-4.95 4.95 4.95 4.95-1.414 1.414-4.95-4.95-4.95 4.95-1.414-1.414 4.95-4.95-4.95-4.95L7.05 5.636z" fill="currentColor" />
                                    </svg>
                                </span>
                            </button>
                        </div> : null
                    }
                    <div className="block-center badge bg-secondary text-gray-800 rounded-pill border me-2 mb-2">
                        Reset
                        <button className="btn p-0 ms-1" onClick={this.props.resetFilter}>
                            <span className="svg-icon me-0">
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                    <path fill="none" d="M0 0h24v24H0z" />
                                    <path d="M12 10.586l4.95-4.95 1.414 1.414-4.95 4.95 4.95 4.95-1.414 1.414-4.95-4.95-4.95 4.95-1.414-1.414 4.95-4.95-4.95-4.95L7.05 5.636z" fill="currentColor" />
                                </svg>
                            </span>
                        </button>
                    </div>
                </div>
            </div> : null}
        </>
    }

}
export default FilterStrip